import React, {FC, useState} from 'react';
import {Button, Typography} from "@mui/material";
import styled from "styled-components";
import ModalComponent from "../UI/ModalComponent";

const GameInfoContainer = styled.div`
  background-color: #f9f9f9;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  font-size: 18px;
  margin-bottom: 5px;
  height: 1%;

  @media (min-width: 635px) {
    font-size: 22px;
  }

  * {
    margin: 5px;
  }
`;

const GameInfoButtons = styled.div`
  display: flex;
  flex-direction: column;
  
  button {
    margin-bottom: 5px;
  }
`;


type GameInfoComponentProps = {
    seconds : number,
    minutes : number,
    hours : number,
    movesCount : number,
    newGame : () => void,
    restartGame : () => void
}

const modalStyle = {
    position: 'absolute' as 'absolute',
    top: '50%',
    left: '50%',
    transform: 'translate(-50%, -50%)',
    bgcolor: 'background.paper',
    border: '2px solid #000',
    boxShadow: 24,
    p: 4,
};


const GameInfoComponent : FC<GameInfoComponentProps> = ({seconds, minutes, hours, movesCount, newGame, restartGame}) => {
    const [modalIsOpen, setOpen] = useState(false);
    const bestScore = localStorage.getItem("bestScore");
    const bestTime = localStorage.getItem("bestTime");
    const bestTimeString = bestTime ? Math.floor(parseInt(bestTime) / 3600) + ":" + Math.floor((parseInt(bestTime) % 3600) / 60) + ":" + (parseInt(bestTime) % 3600) % 60 : "";

    return (
        <GameInfoContainer>
            <ModalComponent open={modalIsOpen} onClose={() => setOpen(false)}>
                    <Typography id="modal-modal-title" variant="h6" component="h2">
                          
                    </Typography>
                    <Typography id="modal-modal-description" sx={{ mt: 2 }}>
                        <Typography component={"span"} variant="body1">    52 :</Typography>
                        <br/>
                        <ul>
                            <li>       ,       </li>
                            <li>          </li>
                            <li>             </li>
                        </ul>
                        <br/>
                        <Typography component={"span"} variant="body1" style={{marginBottom: "10 px"}}>         ,              (  - ,  - ,     ,  - ).     "",      </Typography>
                        <br/>
                        <Typography component={"span"} variant="body1">   : , , , .</Typography>
                    </Typography>
            </ModalComponent>
            <div>
                <div>: {hours}:{minutes}:{seconds}</div>
                <div>: {movesCount}</div>
                <div> : {bestScore || ""}</div>
                <div> : {bestTimeString}</div>
            </div>
            <GameInfoButtons>
                <Button onClick={restartGame} variant="contained"> </Button>
                <Button onClick={newGame} variant="contained"> </Button>
                <Button variant="contained" onClick={() => setOpen(true)}></Button>
            </GameInfoButtons>
        </GameInfoContainer>
    );
};

export default GameInfoComponent;